body {
  /* background-color: #282c34; */
  background-color: #282c34;
}

body,
html {
  height: 100%;
  /* won't really work cuz the actual page shrinks scroll-behavior: smooth; */
  scroll-behavior: smooth;
}

/* Caraosul ting for croping imgs*/
.thumb-post img {
  object-fit: none; /* Do not scale the image */
  object-position: center; /* Center the image within the element */
  width: 100%;
  height: 80vh;
  margin-bottom: 1rem;
  object-fit: cover; /* crops image and fits to the whole div!*/
  opacity: 0.65;
}
/* background: fixed center no-repeat; */

/* https://images.pexels.com/photos/1294671/pexels-photo-1294671.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940 */
.myj1 {
  background: fixed center
    url(https://images.pexels.com/photos/1294671/pexels-photo-1294671.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940);
  background-size: 50vw;
  transition: 1s ease;
  color: rgb(228, 200, 200);
  height: 80vh;
  opacity: 0.5;
  text-shadow: 2px 1.5px 7px lightblue;
}

.myj1:hover {
  transition: 1s ease;
  color: rgb(131, 228, 255);
  font-size: large;
  background-size: 55vw;
  opacity: 1;
  text-shadow: 2px 1.5px 10px rgba(53, 2, 255, 0.891);
  height: 85vh;
  cursor: none;
}

.myj1-img1 {
  height: 30vh;
  opacity: 0.98;
  transform: rotate(-45deg);
  transition: 1s ease;
}

.myj1-img1:hover {
  transform: rotate(30deg);
  transition: 1s ease;
}

